.searchContainer{
display: flex;
justify-content: center;
}

.searchBox{
position: relative;
}

.searchInput{
height: 30px;
border-radius: 10px;
padding: 5px 30px 5px 10px;
font-size: 1.2rem;
border: none;
}

.searchInput:focus{
    box-shadow: 0 0 20px #ebbed4;
}

.searchButton:hover{
    opacity: .5;
}

.searchButton{
position: absolute;
right: 0;
top: 0;
cursor: pointer;
height: 100%;

border: none;
background: none;
}